import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer, withText } from 'preact-i18n';

import Modal from '../modal';
import WorkoutComments from '../../components/workout/comments';
import WorkoutAddComment from '../../components/workout/commentAdd';

function getWorkoutColor(workout) {
	switch (workout.type) {
		case 1:
			return 'border-primary';	
		case 2:
			return 'border-danger';
		case 3:
			return 'border-success';
		case 4:
			return 'border-info';
		default:
			return 'border-warning';
	}
}

function getWorkoutIcon(workout) {
	switch (workout.type) {
		case 1:
			return 'fas fa-running';
		case 2:
			return 'fas fa-flag-checkered';
		case 3:
			return 'fas fa-hiking';
		case 4:
			return 'fas fa-futbol';
		default:
			return 'fas fa-hiking';
	}
}

const initialState = {
	showAddComment: false,
};

@observer
class Workout extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
        this.updateTimer = {};
		this.inputRef = {};
    }

    showModal = (e) => {
        e.stopPropagation();
        e.preventDefault();
        const img = e.target.dataset.image;
        const imgXXL = e.target.dataset.imagexxl;
        const thumb = e.target.dataset.thumb;
        const name = e.target.dataset.name;
        const title = e.target.dataset.title;
        const date = e.target.dataset.date;
        this.setState({
            displayModal: true,
            currentImage: img,
            currentImageXXL: imgXXL,
            thumb,
            name,
            title,
            date,
        });
    }

    closeModal = () => {
        this.setState({
            displayModal: false,
        });
    }

    toggleDate = (e) => {
        e.stopPropagation();
        e.preventDefault();
        const { showDate } = this.state;
        this.setState({ showDate: !showDate });
    }


	likeWorkout = async () => {
		const { workoutStore } = this.props.stores;
        const { workout } = this.props;
		const response = await workoutStore.likeWorkout({ id: workout.id });

        if (response.status === 200) {
			
		}
	}

	toggleAddComment = (e) => {
		const workoutId = e.target.parentElement.dataset.id || e.target.dataset.id;
		const { showAddComment } = this.state;
		this.setState({
			showAddComment: !showAddComment,
		}, () => {
			if (this.inputRef[workoutId]) {
				this.inputRef[workoutId].focus();
			}
		});
	}

    render() {
        const { displayModal, currentImage, currentImageXXL, showDate, thumb, name, title, date, showAddComment } = this.state;
        const { workout, showDate: forceShowDate } = this.props;
		const { userStore } = this.props.stores;
        const { user } = userStore;
        const intensity = userStore.findIntensity(workout.intensity) || {};
        const teamName = userStore.findTeam(workout.team)
        const { likes = [], comments = [] } = workout;
		const hasLikedWorkout = workout.likes && workout.likes.findIndex(e => e.user === user.id) > -1;

        return (
            <div class='w-100 mb-5'>
                <div class='row'>
                    {displayModal && <Modal close={this.closeModal} image={currentImage} imagexxl={currentImageXXL} thumb={thumb} name={name} title={title} date={date} />}

                    <div class='col-12 col-sm-8 col-lg-6 offset-sm-2 offset-lg-3 d-flex mb-0 position-relative'>
                        {/* {JSON.stringify(workout, null, 4)} */}
                        <div class={`text-center border rounded-circle imageRounded ${getWorkoutColor(workout)}`} style={`${workout.image ? `background-image: url("${workout.image.s3SmallLink}"); background-size: cover;` : ''} border-width: 3px !important;`}>
                            {!workout.image && <i class={`${getWorkoutIcon(workout)} text-${intensity.class ? intensity.class : 'muted'} mt-3`} style='font-size: 40px;' />}
                        </div>
                        <div class='flex-grow-1 pl-3' style='line-height: 1.2em;'>
                            <a class='stretched-link' href={`/workouts/${workout.id}`}>
                                <h5 class='my-0 mb-1' style='line-height: 1.0em;'>{workout.name}</h5>
                            </a>
                            {teamName && <><span class='font-weight-light'>{teamName}</span><br /></>}
                            <small class='text-muted'>
                                {workout.date && <small onClick={this.toggleDate}>
                                    {forceShowDate || showDate ? util.formatDate(workout.date, { locale: 'no-NB' }) : util.formatDistance(workout.date, new Date(), { locale: 'no-NB' })}
                                </small>}
                                <span class={`badge badge-${intensity.class} ml-2`}>{intensity.name}</span>
                                <br />
                            </small>
                            <small>
                                <span class='text-muted'>
                                    {workout.garminId && <div class={`float-left mr-2 text-center border rounded-circle imageRounded imageRoundedIcon`} style={`background-image: url("./assets/garmin-connect.png");`} />}
                                    {workout.stravaId && <div class={`float-left mr-2 text-center border rounded-circle imageRounded imageRoundedIcon`} style={`background-image: url("./assets/strava-icon.png");`} />}

                                    <i class='fas fa-tools' /> {userStore.findEquipment(workout.equipment) || workout.equipment}
                                    <i class='fas fa-road ml-2' /> {util.format(workout.distanceKm, 1)}km
                                    <i class='fas fa-mountain ml-2' /> {util.format(workout.elevation, 0)}m
                                    <i class='fas fa-tachometer-alt ml-2' /> {util.format(workout.speedAvg, 1)}km/t
                                    {workout.dogs && <span><i class='fas fa-dog ml-2' /> {workout.dogs.length}</span>}
                                    <span><i class={`fas fa-map-marked text-${workout.track > 0 ? 'success' : 'danger'} ml-2`} /></span>
                                    <span><i class={`fas fa-crosshairs text-${workout.dogsLeft && workout.dogsRight ? 'success' : 'danger'} ml-2`} /></span>
                                </span><br />
                                <span class='text-muted'>
                                    <i class='fas fa-balance-scale' /> {util.format(workout.kcalBurnedTotal)} kcal
                                    <i class='fas fa-drumstick-bite ml-2' /> {util.format(workout.kcalMeatGramsTotal / 1000, 1)} kg
                                    <i class='fas fa-fish ml-2' /> {util.format(workout.kcalSalmonGramsTotal / 1000, 1)} kg
                                    <i class='fas fa-piggy-bank ml-2' /> {util.format(workout.kcalFatGramsTotal / 1000, 1)} kg
                                </span>
                                <i>{workout.url}</i><br />
                            </small>
                        </div>
                    </div>

                    <div class='col-12 col-sm-8 col-lg-6 offset-sm-2 offset-lg-3 mt-1' style='padding-left: 122px;'>
                        <div class='row'>
                            {workout.images && workout.images.map(img => (
                                <div class={`text-center border rounded imageRounded imageRoundedSmall`}
                                    style={img ? `background-image: url("${img.s3SmallLink}");` : ''}
                                    onClick={this.showModal}
                                    data-image={img.s3LargeLink}
                                    data-imagexxl={img.s3XXLargeLink}
                                    data-thumb={workout.image && workout.image.s3SmallLink}
                                    data-name={userStore.findTeam(workout.team)}
                                    data-title={workout.name}
                                    data-date={workout.date}
                                >
                                    &nbsp;
                                </div>
                            ))}
                        </div>
                    </div>

                    <div class='col-10 offset-2 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-3'>
						{likes && likes.length > 0 && <small class='bg-light shadow-sm rounded-lg py-0 px-1' style='bottom: 20px; right: 15px;'>
							<i class='fas fa-thumbs-up text-primary' /> {likes.length}
						</small>}
						{comments && comments.length > 0 && <small class='float-right bg-light shadow-sm rounded-lg py-0 px-1' style='bottom: 20px; right: 15px;'>
							<i class='fas fa-comment text-primary' /> {comments.length}
						</small>}
					</div>

					<div class='col-10 offset-2 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-2 mb-3'>
						<div class='row'>
							<div class='col-6 text-center'>
								<button type='button' class={`btn btn-sm btn-outline-${hasLikedWorkout ? 'primary' : 'secondary'} btn-lg btn-block ${hasLikedWorkout ? 'text-primary' : ''}`}
									onClick={this.likeWorkout}
									// disabled={hasLikedWorkout}
								>
									<i class={`${hasLikedWorkout ? 'text-primary fas' : 'far'} fa-thumbs-up`} /> Like
								</button>
							</div>
							<div class='col-6 text-center'>
								<button type='button' class='btn btn-sm btn-outline-secondary btn-lg btn-block'
									data-id={workout.id}
									onClick={this.toggleAddComment}
								>
									<i class='fas fa-comment' /> Comment
								</button>
							</div>
						</div>
					</div>

                    {showAddComment && <WorkoutAddComment
						stores={this.props.stores}
						workout={workout}
						inputRef={c => this.inputRef[workout.id] = c}
                        col='10'
                        offset='2'
					/>}
					<WorkoutComments stores={this.props.stores} workout={workout} col='10' offset='2' />

                </div>
            </div>
        );
    }
}

export default Workout;
