import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';
import linkstate from 'linkstate';

import AdminNotification from '../../components/admin/notification';
import WeeklyTotals from '../../components/admin/weeklyTotals';
import KeyNumbers from '../../components/admin/keyNumbers';
import NewsDetail from '../../components/news/detail';

import mu from '../../lib/musher-util';
const MARGIN_TOP = mu.marginTop(true);
const MARGIN_BOTTOM = mu.marginBottom();
const SUB_MENU_MARGIN_TOP = mu.subMenuMarginTop();

const initialState = {
	isLoading: false,
	email: '',
};

@observer
class Admin extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

	async loadAll() {
		this.setState({ isLoading: true });
		await this.loadStats();
		this.setState({ isLoading: false });
    }

	loadStats = async () => {
		this.setState({ isLoading: true });
		const { adminStore } = this.props.stores;
		await adminStore.getStats();
		this.setState({ isLoading: false });
	}

    componentDidMount() {
        this.loadAll();
    }

	simulateUser = (e) => {
		const { email } = this.state;
        const { userStore } = this.props.stores;
		userStore.getSimulateUser(email);
	}

	setQuery = (e) => {
		const { notification = {} } = this.state;
		notification.query = e.target.innerText;
		notification.url = e.target.dataset.url;
		this.setState({ notification });
	}

	setWorkoutQuery = (e) => {
		const { notification = {} } = this.state;
		notification.workoutQuery = e.target.innerText;
		notification.url = e.target.dataset.url;
		this.setState({ notification });
	}

	sendNotification = () => {
        const { adminStore } = this.props.stores;
		const { notification } = this.state;
		adminStore.sendNotification({ ...notification });
	}

	saveNews = () => {
        const { newsStore } = this.props.stores;
		const { news } = this.state;
		newsStore.insert({ ...news });
	}

	render() {
		const { appState, adminStore, userStore } = this.props.stores;
		const { notification = {}, news = {} } = this.state;
        const { darkmode } = appState;
		const { isAdmin, user } = userStore;
        const { stats } = adminStore;
        const lastStat = stats[0] || {};

		return (
			<div class='container-fluid' style={`margin-bottom: ${MARGIN_BOTTOM}; margin-top: ${MARGIN_TOP};`}>
				<div class='row mb-4 fixed-top' style={`margin-top: ${SUB_MENU_MARGIN_TOP}; background-color: ${darkmode ? '#191d21' : '#f8f9fa'};`}>
					{isAdmin && <div class='col text-center border-bottom border-primary subtopmenu'>
						<a href='/admin' class='text-primary stretched-link'><nobr><i class='fas fa-user-shield' /> <Text id='home.admin'>Admin</Text></nobr></a>
					</div>}
					<div class='col text-center subtopmenu'>
						<a href='/' class='text-secondary stretched-link'><nobr><i class='fas fa-chart-line' /> <Text id='todo.dashboard'>Dashboard</Text></nobr></a>
					</div>
					<div class='col text-center subtopmenu'>
						<a href='/feed' class='text-secondary stretched-link'><nobr><i class='fas fa-rss' /> <Text id='todo.feed'>Feed</Text></nobr></a>
					</div>
					<div class='col text-center subtopmenu'>
						<a href='/todo' class='text-secondary stretched-link'><nobr><i class='fas fa-list' /> <Text id='todo.todo'>Todo</Text></nobr></a>
					</div>
				</div>

				<div class='row border-bottom'>
					<div class='col-12 mb-4'>
						<h5>Add news</h5>
						<div class='form-row align-items-center'>
							<div class='col-12'>
								<label class='sr-only' for='inlineFormInputLanguage'> Title</label>
								<div class='input-group mb-2'>
									<div class='input-group-prepend'>
										<div class='input-group-text'>Title</div>
									</div>
									<input type='text' class='form-control' id='inlineFormInputLanguage' placeholder='News story title' onInput={linkstate(this, 'news.title')} value={news.title} />
								</div>
							</div>
							<div class='col-12'>
								<label class='sr-only' for='inlineFormInputLanguage'> Ingress</label>
								<div class='input-group mb-2'>
									<div class='input-group-prepend'>
										<div class='input-group-text'>Ingress</div>
									</div>
									<textarea type='text' class='form-control' id='inlineFormInputLanguage' placeholder='News story ingress' onInput={linkstate(this, 'news.ingress')} value={news.ingress} />
								</div>
							</div>
							<div class='col-12'>
								<label class='sr-only' for='inlineFormInputLanguage'> Body</label>
								<div class='input-group mb-2'>
									<div class='input-group-prepend'>
										<div class='input-group-text'>Body</div>
									</div>
									<textarea type='text' rows={10} class='form-control' id='inlineFormInputLanguage' placeholder='News story body' onInput={linkstate(this, 'news.body')} value={news.body} />
								</div>
							</div>
							<div class='col-12'>
								<button type='button' class='btn btn-primary mb-2' onclick={this.saveNews}>Save news</button>
							</div>
						</div>

						<h5>Preview</h5>
						<NewsDetail news={{ ...news }} />
					</div>

					<div class='col-12 mb-4'>
						<h5>Send notification</h5>
						<div class='form-row align-items-center'>
							<div class='col-12'>
								<label class='sr-only' for='inlineFormInputLanguage'> Language</label>
								<div class='input-group mb-2'>
									<div class='input-group-prepend'>
										<div class='input-group-text'>Lang</div>
									</div>
									<input type='text' class='form-control' id='inlineFormInputLanguage' placeholder='Notification language: en, no' onInput={linkstate(this, 'notification.language')} value={notification.language} />
								</div>
							</div>
							<div class='col-12 mt-2 mb-4'>
								<label class='sr-only' for='inlineFormInputLanguage'>User Query</label>
								<div class='input-group mb-2'>
									<div class='input-group-prepend'>
										<div class='input-group-text'>User Query</div>
									</div>
									<input type='text' class='form-control' id='inlineFormInputLanguage' placeholder='Mongodb user query: { "id": 2 }' onInput={linkstate(this, 'notification.query')} value={notification.query} />
								</div>
								<div class='text-muted'>
									<div><code onClick={this.setQuery} data-url='/users'>{'{ "garmin.access.oauth_token": { "$exists": true } }'}</code></div>
									<div><code onClick={this.setQuery} data-url='/users'>{'{ "strava.access_token": { "$exists": true } }'}</code></div>
									<div><code onClick={this.setQuery} data-url='/users/edit'>{'{ "username": { "$eq": "" } }'}</code></div>
									<div><code onClick={this.setQuery} data-url='/users/edit'>{'{ "firstname": { "$eq": "" } }'}</code></div>
									<div><code onClick={this.setQuery} data-url='/users'>{'{ "images": { "$exists": false } }'}</code></div>
									<div><code onClick={this.setQuery} data-url='/users'>{'{ "termsAndCondition": { "$eq": "" } }'}</code></div>
									<div><code onClick={this.setQuery} data-url='/users'>{'{ "settings.summaryEmails": { "$exists": false } }'}</code></div>
									<div><code onClick={this.setQuery} data-url='/users'>{'{ "settings.infoEmails": { "$exists": false } }'}</code></div>
									<div><code onClick={this.setQuery} data-url='/users'>{'{ "settings.partnerEmails": { "$exists": false } }'}</code></div>
								</div>
							</div>
							<div class='col-12 mt-2 mb-4'>
								<label class='sr-only' for='inlineFormInputLanguage'>Workout Query</label>
								<div class='input-group mb-2'>
									<div class='input-group-prepend'>
										<div class='input-group-text'>Workout Query</div>
									</div>
									<input type='text' class='form-control' id='inlineFormInputLanguage' placeholder='Mongodb query: { "id": 2 }' onInput={linkstate(this, 'notification.workoutQuery')} value={notification.workoutQuery} />
								</div>
								<div class='text-muted'>
									<div><code onClick={this.setWorkoutQuery}>{'{ "username": { "$eq": "" } }'}</code></div>
								</div>
							</div>
							<div class='col-12'>
								<label class='sr-only' for='inlineFormInputMessage'> Message</label>
								<div class='input-group mb-2'>
									<div class='input-group-prepend'>
										<div class='input-group-text'>M</div>
									</div>
									<input type='text' class='form-control' id='inlineFormInputMessage' placeholder='Notification message' onInput={linkstate(this, 'notification.message')} value={notification.message} />
								</div>
							</div>
							<div class='col-12'>
								<label class='sr-only' for='inlineFormInputTitle'> Title</label>
								<div class='input-group mb-2'>
									<div class='input-group-prepend'>
										<div class='input-group-text'>T</div>
									</div>
									<input type='text' class='form-control' id='inlineFormInputTitle' placeholder='Notification title' onInput={linkstate(this, 'notification.title')} value={notification.title} />
								</div>
							</div>
							<div class='col-12'>
								<label class='sr-only' for='inlineFormInputUrl'> URL</label>
								<div class='input-group mb-2'>
									<div class='input-group-prepend'>
										<div class='input-group-text'>URL</div>
									</div>
									<input type='text' class='form-control' id='inlineFormInputUrl' placeholder='/news/123' onInput={linkstate(this, 'notification.url')} value={notification.url} />
								</div>
							</div>
							<div class='col-12'>
								<label class='sr-only' for='inlineFormInputUrlList'> URL List</label>
								<div class='input-group mb-2'>
									<div class='input-group-prepend'>
										<div class='input-group-text'>URL List</div>
									</div>
									<input type='text' class='form-control' id='inlineFormInputUrlList' placeholder='/news/' onInput={linkstate(this, 'notification.urlList')} value={notification.urlList} />
								</div>
							</div>
							<div class='col-12'>
								<label class='sr-only' for='inlineFormInputUrlList'> Do send</label>
								<div class='input-group mb-2'>
									<div class='input-group-prepend'>
										<div class='input-group-text'>Do send</div>
									</div>
									<input type='text' class='form-control' id='inlineFormInputUrlList' placeholder='' onInput={linkstate(this, 'notification.doSend')} value={notification.doSend} />
								</div>
							</div>
							<div class='col-12'>
								<button type='button' class='btn btn-primary mb-2' onclick={this.sendNotification}>Send notification</button>
							</div>
						</div>

						<h5>Preview</h5>
						<AdminNotification notification={{ ...notification, addedBy: { ...user } }} />
					</div>

					<div class='col-12 mb-4'>
						<WeeklyTotals stores={this.props.stores} />
					</div>

					<div class='col-12 mb-4'>
						<KeyNumbers stores={this.props.stores} />
					</div>
					<div class='col-12 mb-4'>
                        <table class='table table-striped'>
                            <tbody>
	                            <tr><th>createdDate</th><td>{util.isoDate(lastStat.createdDate)}</td></tr>
	                            <tr><th>dogs</th><td>{lastStat.dogs}</td></tr>
	                            <tr><th>notifications</th><td>{lastStat.notifications}</td></tr>
	                            <tr><th>teams</th><td>{lastStat.teams}</td></tr>
	                            <tr><th>workoutPlans</th><td>{lastStat.workoutPlans}</td></tr>
	                            <tr><th>totalDistance</th><td>
                                    {util.format(lastStat.totalDistance)} km<br />
                                    {util.format(lastStat.totalDistance / 40075, 2)} <span class='text-muted'>times around the world</span><br />
                                    {util.format(lastStat.totalDistance / 4509, 2)} <span class='text-muted'>times accross USA</span><br />
                                    {util.format(lastStat.totalDistance / 4700, 2)} <span class='text-muted'>times accross Europe</span><br />
                                    {util.format(lastStat.totalDistance / 1729, 1)} <span class='text-muted'>times from Oslo to Alta</span><br />
                                    {util.format(lastStat.totalDistance / 384400, 3)} <span class='text-muted'>times to the moon</span><br />
                                    {util.format(lastStat.totalDistance / 400, 1)} <span class='text-muted'>recharges (400 km range)</span><br />
                                </td></tr>
								{/* <tr><th>totalDistanceEquipment</th><td>{JSON.stringify(lastStat.totalDistanceEquipment)}</td></tr> */}
	                            <tr><th>totalElevation</th><td>
                                    {util.format(lastStat.totalElevation)} m<br />
                                    {util.format(lastStat.totalElevation / 8848, 1)} <span class='text-muted'>times to the top of Mount Everest</span><br />
                                </td></tr>
	                            <tr><th>totalDuration</th><td>
                                    {util.secToHms(lastStat.totalDuration)}<br />
                                    {Math.floor(lastStat.totalDuration / 86400)} days, &nbsp;
                                    {Math.floor(lastStat.totalDuration % 86400 / 3600)} hours
                                </td></tr>
	                            <tr><th>tracks</th><td>{lastStat.tracks}</td></tr>
	                            <tr><th>users</th><td>{lastStat.users}</td></tr>
	                            <tr><th>usersGarmin</th><td>{lastStat.usersGarmin}</td></tr>
	                            <tr><th>usersStrava</th><td>{lastStat.usersStrava}</td></tr>
	                            <tr><th>workouts</th><td>{lastStat.workouts}</td></tr>

                                <tr><th>avgDistance</th><td>{util.format(lastStat.avgDistance, 1)} km</td></tr>
                                {/* <tr><th>avgDistanceEquipment</th><td>{JSON.stringify(lastStat.avgDistanceEquipment)}</td></tr> */}
	                            <tr><th>avgDogs</th><td>{util.format(lastStat.avgDogs, 1)}</td></tr>
	                            <tr><th>avgDuration</th><td>{util.secToHms(lastStat.avgDuration)}</td></tr>
	                            <tr><th>avgSpeed</th><td>{util.format(lastStat.avgSpeed, 1)} km/t</td></tr>
	                            <tr><th>avgElevation</th><td>{util.format(lastStat.avgElevation, 1)} m</td></tr>
	                            <tr><th>avgTemperature</th><td>{util.format(lastStat.avgTemperature, 1)} C</td></tr>

	                            <tr><th>dogsHistoryAnimalchiropractor</th><td>{lastStat.dogsHistoryAnimalchiropractor}</td></tr>
	                            <tr><th>dogsHistoryBodyScore</th><td>{lastStat.dogsHistoryBodyScore}</td></tr>
	                            <tr><th>dogsHistoryClawClip</th><td>{lastStat.dogsHistoryClawClip}</td></tr>
	                            <tr><th>dogsHistoryDeworming</th><td>{lastStat.dogsHistoryDeworming}</td></tr>
	                            <tr><th>dogsHistoryDiarrhea</th><td>{lastStat.dogsHistoryDiarrhea}</td></tr>
	                            <tr><th>dogsHistoryFight</th><td>{lastStat.dogsHistoryFight}</td></tr>
	                            <tr><th>dogsHistoryFoodLeftover</th><td>{lastStat.dogsHistoryFoodLeftover}</td></tr>
	                            <tr><th>dogsHistoryInjury</th><td>{lastStat.dogsHistoryInjury}</td></tr>
	                            <tr><th>dogsHistoryMassage</th><td>{lastStat.dogsHistoryMassage}</td></tr>
	                            <tr><th>dogsHistoryMating</th><td>{lastStat.dogsHistoryMating}</td></tr>
	                            <tr><th>dogsHistoryMaturity</th><td>{lastStat.dogsHistoryMaturity}</td></tr>
	                            <tr><th>dogsHistoryNewStraw</th><td>{lastStat.dogsHistoryNewStraw}</td></tr>
	                            <tr><th>dogsHistoryOther</th><td>{lastStat.dogsHistoryOther}</td></tr>
	                            <tr><th>dogsHistorySick</th><td>{lastStat.dogsHistorySick}</td></tr>
	                            <tr><th>dogsHistoryStoolProblem</th><td>{lastStat.dogsHistoryStoolProblem}</td></tr>
	                            <tr><th>dogsHistoryUnwantedMating</th><td>{lastStat.dogsHistoryUnwantedMating}</td></tr>
	                            <tr><th>dogsHistoryUpdate</th><td>{lastStat.dogsHistoryUpdate}</td></tr>
	                            <tr><th>dogsHistoryVaccine</th><td>{lastStat.dogsHistoryVaccine}</td></tr>
	                            <tr><th>dogsHistoryVeterinarianVisit</th><td>{lastStat.dogsHistoryVeterinarianVisit}</td></tr>
	                            <tr><th>dogsHistoryWeek</th><td>{lastStat.dogsHistoryWeek}</td></tr>
	                            <tr><th>dogsHistoryWeight</th><td>{lastStat.dogsHistoryWeight}</td></tr>
	                            <tr><th>dogsHistoryYear</th><td>{lastStat.dogsHistoryYear}</td></tr>

	                            <tr><th>dogsInjured</th><td>{lastStat.dogsInjured}</td></tr>
	                            <tr><th>dogsMaturity</th><td>{lastStat.dogsMaturity}</td></tr>
	                            <tr><th>dogsMedication</th><td>{lastStat.dogsMedication}</td></tr>
	                            <tr><th>dogsNoBreeding</th><td>{lastStat.dogsNoBreeding}</td></tr>
	                            <tr><th>dogsPregnant</th><td>{lastStat.dogsPregnant}</td></tr>
	                            <tr><th>dogsPuppy</th><td>{lastStat.dogsPuppy}</td></tr>
	                            <tr><th>dogsRelocation</th><td>{lastStat.dogsRelocation}</td></tr>
	                            <tr><th>dogsRetired</th><td>{lastStat.dogsRetired}</td></tr>
	                            <tr><th>dogsSick</th><td>{lastStat.dogsSick}</td></tr>
	                            <tr><th>dogsSterilized</th><td>{lastStat.dogsSterilized}</td></tr>
                            </tbody>
                        </table>
					</div>
				</div>
				<div class='row'>
					<div class='col-12 mb-4'>

						<div class='form-row align-items-center'>
							<div class='col-auto'>
								<label class='sr-only' for='inlineFormInputGroup'>Email</label>
								<div class='input-group mb-2'>
									<div class='input-group-prepend'>
										<div class='input-group-text'>@</div>
									</div>
									<input type='text' class='form-control' id='inlineFormInputGroup' placeholder='Email' onInput={linkstate(this, 'email')} />
								</div>
							</div>
							<div class='col-auto'>
								<button type='button' class='btn btn-primary mb-2' onclick={this.simulateUser}>Simulate user</button>
							</div>
						</div>

						<br />isAdmin: {JSON.stringify(isAdmin)}<br />
						<xmp>user: {JSON.stringify(user, null, 4)}</xmp>
					</div>
				</div>
			</div>
		);
	}
}

export default Admin;
