import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { toJS } from 'mobx';
import { Text, Localizer } from 'preact-i18n';
import { route } from 'preact-router';
import linkstate from 'linkstate';

import mu from '../../lib/musher-util';

import Images from '../../components/form/images';
import Files from '../../components/form/files';

const MARGIN_TOP = mu.marginTop(false);
const MARGIN_TOP_BACK = mu.marginTopBack(false);
const MARGIN_BOTTOM = mu.marginBottom();

const initialState = {
	isLoading: false,
};

@observer
class Input extends Component {
    handleImageErrored = (e) => {
        const image = e.target;
        image.onerror = null;
        setTimeout(() => {
            image.src += `?${new Date()}`;
        }, 1000);
    }

    render() {
        // const { saved, dog, type = 'text', datalist = [], min, max, step, field, title, icon, help, onInput, updateField } = this.props;
        const { saved, news, type = 'text', rows, field, title, icon, help, onInput, updateField } = this.props;
        const value = news[field];
        if (type === 'image') {
            return (
                <div class={`form-group ${saved[`${field}.${news.id}`] ? 'is-valid' : ''}`}>
                    <label for={`${field}Input`}>{icon && <i class={`${icon} text-muted`} />} {title}</label>

                    <div class='container-fluid'>
                        <div class='row'>
                            {news.images && news.images.map(img => (
                                <div class='col-3 p-1'>
                                    <img src={img.s3SmallLink} class='img-fluid' onError={this.handleImageErrored} />
                                </div>
                            ))}
                        </div>
                    </div>
                    <Images saved={saved} object={news} field={field} updateField={updateField} />
                    {saved[`${field}.${news.id}`] ? <div class={`valid-feedback`} style='display: block;'><Text id='news.changes-saved'>Endringene er lagret</Text></div> : 
                        <small id={`${field}Help`} class='form-text text-muted'>{help}</small>}
                </div>
            );
        }
        if (type === 'file') {
            return (
                <div class={`form-group ${saved[`${field}.${news.id}`] ? 'is-valid' : ''}`}>
                    <label for={`${field}Input`}>{icon && <i class={`${icon} text-muted`} />} {title}</label>
                    <Files saved={saved} object={news} field={field} updateField={updateField} />
                    {saved[`${field}.${news.id}`] ? <div class={`valid-feedback`} style='display: block;'><Text id='news.changes-saved'>Endringene er lagret</Text></div> : 
                        <small id={`${field}Help`} class='form-text text-muted'>{help}</small>}
                </div>
            );
        }
        if (type === 'textarea') {
            return (
                <div class={`form-group ${saved[`${field}.${news.id}`] ? 'is-valid' : ''}`}>
                    <label for={`${field}Input`}>{icon && <i class={`${icon} text-muted`} />} {title}</label>
                    <textarea type={type} class={`form-control ${saved[`${field}.${news.id}`] ? 'is-valid' : ''}`} id={`${field}Input`} aria-describedby={`${field}Help`}
                        value={value}
                        rows={rows}
                        data-id={news.id}
                        data-field={field}
                        onInput={onInput}
                    />
                    {saved[`${field}.${news.id}`] ? <div class={`valid-feedback`} style='display: block;'><Text id='news.changes-saved'>Endringene er lagret</Text></div> : 
                        <small id={`${field}Help`} class='form-text text-muted'>{help}</small>}
                </div>
            );
        }

        return (
            <div class={`form-group ${saved[`${field}.${news.id}`] ? 'is-valid' : ''}`}>
                <label for={`${field}Input`}>{icon && <i class={`${icon} text-muted`} />} {title}</label>
                <input type={type} class={`form-control ${saved[`${field}.${news.id}`] ? 'is-valid' : ''}`} id={`${field}Input`} aria-describedby={`${field}Help`}
                    value={value}
                    data-id={news.id}
                    data-field={field}
                    onInput={onInput}
                />
                {saved[`${field}.${news.id}`] ? <div class={`valid-feedback`} style='display: block;'><Text id='news.changes-saved'>Endringene er lagret</Text></div> : 
                    <small id={`${field}Help`} class='form-text text-muted'>{help}</small>}
            </div>
        );
    }
}

@observer
class NewsEdit extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
        this.updateTimer = {};
    }

	async loadNews(props = this.props) {
		this.setState({ isLoading: true });
		const { newsStore } = this.props.stores;
		await newsStore.load(props.id);
	}

    updateField = (e) => {
        const { id, field, type, elementtype } = e.target.dataset;
        const value = e.target.value;
        this.updateFieldValue({ id: parseInt(id, 10), field, value, type, elementtype });
    }

    updateFieldValue = ({ id, field, value, datatype, elementtype, skipTimer = false }) => {
        const { newsStore } = this.props.stores;
        let newValue = value;

        let localObjectValue = value;
        const { news } = newsStore;
        if (field === 'images') {
            if (Array.isArray(news.images) && Array.isArray(value)) {
                localObjectValue = [...news.images, ...value];
            }
        }
    
        if (datatype === 'array' && !Array.isArray(value)) {
            if (elementtype === 'number') {
                newValue = value.split(',').map(e => parseFloat(e) || e);
            } else  {
                newValue = value.split(',');
            }
            // console.log('updateFieldValue', id, field, value, datatype, elementtype);
        }

       
        newsStore.updateField(id, field, localObjectValue);
        if (skipTimer) {
            this.doUpdateField(id, field, newValue);
        } else {
            clearTimeout(this.updateTimer[field]);
            this.updateTimer[field] = setTimeout(async () => {
                this.doUpdateField(id, field, newValue);
            }, 500);
        }
    }

    async doUpdateField(id, field, value) {
        const { newsStore } = this.props.stores;
        await newsStore.saveField(id, field, value);
    }

	componentDidMount() {
		this.loadNews();
	}

    back = () => {
        const { newsStore } = this.props.stores;
		const { news } = newsStore;
        route(`/news/${news.id}`, true);
    }

	render() {
		const { newsStore, userStore } = this.props.stores;
        const { isAdmin } = userStore;
        if (!isAdmin) {
            return '';
        }
		const { news, saved } = newsStore;
		return (
			<div class='container-fluid' style={`margin-bottom: ${MARGIN_BOTTOM}; margin-top: ${MARGIN_TOP};`}>
                <div class='d-flex justify-content-between mb-2'>
                    <div>
                        <button class='btn btn-link pl-0 fixed-top'
                            style={`top: ${MARGIN_TOP_BACK}; left: 5px; font-size: 25px; width: 40px;`}
                            onClick={this.back}>
                            <i class='fas fa-arrow-circle-left' />
                        </button>
                    </div>
                    <div>
                        <h5 class='text-muted'>Siste nytt</h5>
                    </div>
                    <div>
                        &nbsp;
                    </div>
                </div>

				<div class='row'>
					<div class='col-12'>
                        <Input saved={saved} news={news} field='title' icon='fas fa-heading'
                            title={<Text id='news.title'>Title</Text>}
                            help={<Text id='news.title-help'>Title of this news story</Text>}
                            onInput={this.updateField} />

                        <Input saved={saved} news={news} field='ingress' icon='fas fa-quote-right'
                            type='textarea'
                            title={<Text id='news.ingress'>Ingress</Text>}
                            help={<Text id='news.ingress-help'>Ingress of this news story</Text>}
                            onInput={this.updateField} />

                        <Input saved={saved} news={news} field='body' icon='fas fa-quote-right'
                            type='textarea' rows={20}
                            title={<Text id='news.body'>Body</Text>}
                            help={<Text id='news.body-help'>Body of this news story</Text>}
                            onInput={this.updateField} />

                        <Input saved={saved} news={news} field='image' type='image' icon='fas fa-camera'
                            title={<Text id='news.photo'>Bilder</Text>} 
                            help={<Text id='news.photo-help'>Images for this news story.</Text>}
                            onInput={this.updateField} updateField={this.updateFieldValue} />


					</div>
				</div>
			</div>
		);
	}
}

export default NewsEdit;
