import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';

@observer
class Teams extends Component {
    toggleTeam = (team) => {
		const { callback } = this.props;
        const { dogStore, workoutStore } = this.props.stores;
		const { currentTeam } = dogStore;
		if (team === currentTeam) {
			dogStore.setCurrentTeam(undefined);
			workoutStore.setCurrentTeam(undefined);
		} else {
			dogStore.setCurrentTeam(team);
			workoutStore.setCurrentTeam(team);
		}
		if (util.isDefined(callback)) {
			callback();
		}
	}

    render() {
        const { userStore, dogStore } = this.props.stores;
        const { teams } = userStore;
		const { currentTeam } = dogStore;
        return (
			<div class='container-fluid px-0 py-2'>
				<div class='row'>
					{teams && teams.map(team => (
						<div class='col-6 mb-1 px-1' key={team.id}>
							<button
								class={`btn btn-sm btn-block ${currentTeam === team.id ? 'btn-success' : 'btn-outline-success'} text-nowrap`}
								style='text-overflow: ellipsis; overflow: hidden;'
								onClick={() => this.toggleTeam(team.id)}
							><i class='fas fa-users' /> {team.name}</button>
						</div>
					))}
				</div>
			</div>
        );
    }
}

export default Teams;
